import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MoveInOutService } from 'src/app/api/controllers/MoveInOut';
import { MatDrawer } from '@angular/material/sidenav';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { NgForm } from '@angular/forms';

@Component({
	selector: 'app-movein-report-data',
	templateUrl: './movein-report-data.component.html',
	styleUrls: ['./movein-report-data.component.scss']
})
export class MoveinReportDataComponent implements OnInit {

	pageName: string = "";
    reportsDataList: any;
    visitorReportData: any;
    totalItems: any;
    isDataLoaded: boolean = false;
    unitBlocksData: any;
    blockId = null;
    blockNo: string = "";
    end_date = null;
    start_date = null;
    visitByData: any;
    visitTypeId = null;
    listData: any;
    columnData:any;
    gridSourceData:any;
    reportData:any = "";
    isReportSubmitted:boolean = false;
    @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
    @ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;

    filterSelected:string = "all";
	  singleBlock:string = "Select Tower";
    selectedBlockId:number;
    
    timeZone:any;

    fromDate:any;
    toDate:any;
    @ViewChild('f') myForm: NgForm;

    allLink: boolean = false;
    
    constructor(
        private route: ActivatedRoute, 
        private apartmentService: ApartmentService,
        private moveInOutService: MoveInOutService, 
        private sharedService: SharedService, 
        private sessionService: SessionService) 
        { }
    
    
    isMobileView() {
        return window.innerWidth <= 767 ? 'table-responsive' : '';
    }

    getPrintParams(event){
        this.datagrid.exportdata(event, 'MoveinReportData');
      }
    
      goBack() {
        this.matDrawer.close();
      }
    
    getDate(date) {
        return moment(date).add(this.timeZone.offset, 'hours').format(this.timeZone.date);
    }

    getStartDate(event) {
      this.start_date = event
    }

    getEndDate(event) {
      this.end_date = event
    }
    
    submitReportsFilterForm() {
        let params = {
            ApartmentID : parseInt(this.sessionService.apartmentId),
            ApartmentBlockID : parseInt(this.blockId),
            StartDate : this.getDate(this.start_date),
			      EndDate : this.getDate(this.end_date)
		    }
        this.getDetails(params);
    }

    getDetails(params){    
        let serviceName;
        
		let id = parseInt(this.route.params['value'].id);
		let name_ = this.route.params['value'].name;
        if(id == 372){
			params.MoveInStatusID = id;
            serviceName = this.moveInOutService.getReportsForMoveInUsersMultiFilter(params)
        }else if(id == 373){
			 params.MoveOutStatusID = id;
            serviceName = this.moveInOutService.getReportsForMoveOutUsersMultiFilter(params)
        }else if(id == 374){
			params.MoveInStatusID = id;
            serviceName = this.moveInOutService.getReportsForMoveInUsersMultiFilter(params)
        }else if(name_ == "rejectedmoveout"){
			params.MoveOutStatusID = id;
            serviceName = this.moveInOutService.getReportsForMoveOutUsersMultiFilter(params)
        }
        else if(name_ == "moveout"){
			params.MoveOutStatusID = id;
            serviceName = this.moveInOutService.getReportsForMoveOutUsersMultiFilter(params)
        } else if(name_ == 'moveinusers'){
			params.MoveInStatusID = id;
            serviceName = this.moveInOutService.getReportsForMoveInUsersMultiFilter(params)
        }
        else if(name_ == 'cancelledmovein'){
			params.MoveInStatusID = id;
            serviceName = this.moveInOutService.getReportsForMoveInUsersMultiFilter(params)
        }
        else if(name_ == 'delayedMoveIn'){
            serviceName = this.moveInOutService.getReportsForMoveInDelayedUsersMultiFilter(params)
        }else if(name_ == 'delayedMoveOut'){
            serviceName = this.moveInOutService.getReportsForMoveOutDelayedUsersMultiFilter(params)
        }
        serviceName.subscribe((res:any) => {
              
            if(res.errorMessage){
			this.reportsDataList = [];
			this.totalItems =  this.reportsDataList.length;
             
          }else{
            this.reportsDataList = res;
            this.totalItems =  this.reportsDataList.length;     
           this.gridSourceData = {
            localdata: this.reportsDataList,
            datatype: "array"
          }
        
          this.listData = new jqx.dataAdapter(this.gridSourceData);
          this.isReportSubmitted = true;
          this.isDataLoaded = true;
          }},
          error => {

            this.isReportSubmitted = true;
          this.isDataLoaded = true;
                });
    
    }

    getExpectedDate(val){
        if(val != ''){
          return moment(val).format("MM/DD/YYYY")
        }else{
          return ''
        }
      }

      onGlSearchFilter(event) {
        if(event != ""){
      
          let filtergroup = new jqx.filter();
          let filter_or_operator = 1;
          let filtervalue = event;
          let filtercondition = 'contains';
          let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
          filtergroup.operator = 'or';
          filtergroup.addfilter(filter_or_operator, filterData);
        
          this.datagrid.showfiltercolumnbackground(false);
        
          this.columnData.forEach(item => {
            if(item.datafield != 'Actions') {
            this.datagrid.addfilter(item.datafield, filtergroup, true);
            }
          })
        
          this.datagrid.applyfilters();
          
          }
          else {
          this.datagrid.clearfilters();
          }
         }

    
      cancel(form){
          form.reset();
     }

   

      getSelectedBlock(event) {
        this.blockId = event[0].apartmentBlockId;
      }

      getSelectedVisit(event) {
        this.visitTypeId = event[0].lookupValueId;
      }
    
      filterApply() {
        this.goBack();
        this.isDataLoaded = false;
        let params = {
            ApartmentID : parseInt(this.sessionService.apartmentId),
            ApartmentBlockID : parseInt(this.blockId),
            StartDate : this.getDate(this.start_date),
			      EndDate : this.getDate(this.end_date)
		    }
        this.getDetails(params);
        
      }
    
      clearFilter() {
        
        this.myForm.form.reset();
        this.blockId = null;
     this.start_date = null;
     this.end_date = null;
     this.datagrid.clearfilters();
     
        // =this.getTicketByAdmin();
        let params = {
            ApartmentID : parseInt(this.sessionService.apartmentId),
            ApartmentBlockID : parseInt(this.blockId),
            StartDate : this.start_date === null ? moment().subtract(5,'months').startOf('month').add(this.timeZone.offset, 'hours').format(this.timeZone.date) : this.getDate(this.start_date),
        EndDate : this.end_date === null ? moment().add(this.timeZone.offset, 'hours').format(this.timeZone.date) : this.getDate(this.end_date)
		    }
        this.getDetails(params);
        this.goBack();
      }
    
    ngOnInit() {

      this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

        this.pageName = this.route.params['value'].title === undefined ? this.route.params['value'].name : this.route.params['value'].title;
        this.allLink = this.route.params['value'].link;
        let unitBlockParams = {
            apartmentId: parseInt(this.sessionService.apartmentId)
        };
        this.apartmentService.getApartmentBlockByApartmentId(unitBlockParams).subscribe((res: any) => {
            this.unitBlocksData = res;
        });
        let visitParams = {
            ApartmentId: this.sessionService.apartmentId,
            LookupTypeId: 15
        }
        
        this.isDataLoaded = false;

        var cellsrenderer = (row, column, value) => {
            return '<div class="jqx-custom-inner-cell">' + value + '</div>';
          }
      
          var columnrenderer = (value) => {
            return '<div style="padding: 14px">' + value + '</div>';
          }
      
      
          this.columnData = [{
               text: 'Resident Name',
               datafield: 'residentName',
               cellsrenderer: cellsrenderer,
               minwidth: 120,
               pinned: true,
               renderer: columnrenderer
           },{
                text: 'Unit No',
                datafield: 'unitNo',
                minwidth: 80,
                cellsrenderer: cellsrenderer,
                renderer: columnrenderer
            },{
               text: 'Tower No',
               datafield: 'blockNo',
               cellsrenderer: cellsrenderer,
               minwidth: 100,
               renderer: columnrenderer
           },  {
               text: 'Owner/Tenant',
               datafield: 'ownerorTenant',
               cellsrenderer: cellsrenderer,
               minwidth: 130,
               renderer: columnrenderer
           }, {
               text: 'Resident Contact Number',
               datafield: 'residentContactNumber',
               cellsrenderer: cellsrenderer,
               minwidth: 220,
               maxwidth: 250,
               renderer: columnrenderer
			},{
				text: 'No of Occupants',
				datafield: 'noOfOccupants',
				cellsrenderer: cellsrenderer,
                minwidth: 130,
                maxwidth: 150,
				renderer: columnrenderer 
			}, {
                text: 'Expected Date',
                datafield: 'expectedDate',
                cellsrenderer: (row, column, value) => {
					return '<div class="jqx-custom-inner-cell">'+ this.getExpectedDate(value) +'</div>'
				  },
                minwidth: 130,
                maxwidth: 150,
                renderer: columnrenderer 
            }, {
                text: 'Expected Time',
                datafield: 'expectedTime',
                cellsrenderer: cellsrenderer,
                minwidth: 120,
                maxwidth: 150,
                renderer: columnrenderer 
            }];
     if(this.route.params['value'].id == '374'){
		 let obj = {
			text: 'Rejection Reason',
			datafield: 'reason',
			cellsrenderer: cellsrenderer,
			minwidth: 170,
			renderer: columnrenderer 
		}
        this.columnData.push(obj)
     }
     this.fromDate =  moment().subtract(12,'months').endOf('month').format(this.timeZone.time);
     this.toDate = moment().format(this.timeZone.time);

     let params = {
        ApartmentID : parseInt(this.sessionService.apartmentId),
        ApartmentBlockID : parseInt(this.blockId),
        StartDate : this.start_date === null ? moment().subtract(5,'months').startOf('month').add(this.timeZone.offset, 'hours').format(this.timeZone.date) : this.getDate(this.start_date),
        EndDate : this.end_date === null ? moment().add(this.timeZone.offset, 'hours').format(this.timeZone.date) : this.getDate(this.end_date)
    }
    this.getDetails(params);

    }

    getAllBlockData(){

	  this.filterSelected = 'all';
	  this.singleBlock = "Select Tower";
	  
	  this.blockId = "";
	  this.datagrid.clearfilters();
  
	}

	getSingleBlock(block){
  	
		this.filterSelected = 'single';
		this.singleBlock = block.apartmentBlockNumber;
		this.blockId = block.apartmentBlockId;

		let filtergroup = new jqx.filter();
			let filter_or_operator = 1;
			let filtervalue = this.singleBlock;
			let filtercondition = 'contains';
			let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
			filtergroup.operator = 'or';
			filtergroup.addfilter(filter_or_operator, filterData);
	  
			this.datagrid.showfiltercolumnbackground(false);
	  
			this.columnData.forEach(item => {
			  if(item.datafield != 'Actions') {
				this.datagrid.addfilter(item.datafield, filtergroup, true);
			  }
			})
	  
			this.datagrid.applyfilters();
	}
}
